#toPrint {

    display: none;

    text-align: center;
    text-transform: uppercase;


    padding-top: 5px;
    padding-bottom: 5px;

    font-size: 1em;

}

@media print {
    div.app {
        display: none;
    }

    #toPrint {
        display: block;
    }
}


.app {
    display: flex;
    flex-direction: row;

    width: 100vw;
    min-height: 100vh;
}

.app ::placeholder {
    text-transform: uppercase;
}

.auth-screen {
    background: rgb(167, 0, 0);
    background-size: cover;

    min-width: 100%;
    min-height: 100vh;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: row;
    align-items: center;
}

.auth-screen-form {

    width: 50%;
    height: 100%;

    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.auth-screen-brand {
    padding: 15px;
    margin-bottom: 15vh;
    text-align: center;
    color: #FFF;
    text-decoration: none;
    width: 100%;
    font-weight: bold;
    font-size: 1.8em;
}

.auth-screen-form input,
textarea {
    width: 100%;
    margin-bottom: 10px;
    padding: 15px;
    box-shadow: 1px 1px 3px 3px rgba(0, 0, 0, .2);
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 1.2em;
    text-align: center;
    box-sizing: border-box;
    text-transform: uppercase;
}

.auth-screen-form #submit {
    text-transform: uppercase;
    font-weight: bold;
    color: #FFF;
    background: rgb(255, 30, 30);
    font-size: 1.3em;
    margin: 0 auto 0 auto;
    cursor: pointer;
    margin-top: 10%;
    margin-bottom: 10%;
    padding: 50px;
}

.auth-screen-form #submit:active {
    outline: none;
    background: rgb(145, 0, 0);
}